import Main from "@/components/Main/Main";
import VueRouter from "vue-router";
import Vue from 'vue';
import News from "@/components/News/News";
import OneNews from "@/components/News/OneNews";

Vue.use(VueRouter);

export default new VueRouter({
    mode: 'history',
    routes: [
        {
            path: '/',
            component: Main
        },
        {
            path: '/news',
            component: News,
        },
        {
            path: '/news/:id',
            component: OneNews
        }
    ]
})