<template>
  <div class="container">
    <h1 class="text-center">{{myNews.title}}</h1>
    <p>{{myNews.text}}</p>
    <button class="btn btn-danger" @click="back">Назад</button>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';
export default {
  name: "OneNews",
  computed: {
    myNews(){
      let news = this.getNews;
      let idNews = this.$route.params.id;
      return news.filter(news => news.id === idNews)[0];
    },
    ...mapGetters(['getNews'])
  },
  methods: {
    back(){
      this.$router.history.push('/news');
    }
  },
  data(){
    return {
      news: [],
      init: false,
    }
  },
  mounted() {
    if(!this.myNews){
      this.$router.history.push('/news');
    }
  }
}
</script>

<style scoped>
  h1, p{
    text-align: center;
  }
</style>