<template>
  <main id="news">
    <div class="container">
      <h1 class="text-center">Новости</h1>
      <div class="news">
        <div :key="news.id" v-for="news of getNewsPage(this.pageNumber)">
          <router-link :to="'/news/' + news.id"><h2>{{news.title}}</h2></router-link>
          <p>{{news.content}}</p>
          <button class="btn btn-danger" @click="deleteNews(news.id)">Удалить</button>
        </div>
      </div>
      <div class="buttons">
        <router-link :key="'button-to__' + i" v-for="(el, i) in getNewsPage()" :to="`/news?id=${i}`"><button>
          {{i}}
        </button></router-link>
      </div>
    </div>
  </main>
</template>

<script>
import {mapActions, mapGetters} from 'vuex';
export default {
  name: "News",
  computed: mapGetters(['getNews']),
  data(){
    return {
      pageNumber: 0,
      pages: [],
      init: false,
    }
  },
  methods: {
    ...mapActions(['deleteNews']),
    update(){
      if(this.$route.query.id){
        this.pageNumber = this.$route.query.id;
      } else {
        this.pageNumber = "0";
      }
    },
    getNewsPage(id){
      let pages = [], size = 4;
      let news = this.getNews.slice();
      while (news.length > 0)
        pages.push(news.splice(0, size));
      if(id){
        return pages[id];
      } else {
        return pages;
      }
    }
  },
  watch: {
    $route: 'update'
  },
  mounted() {
    if(this.$route.query.id){
      this.pageNumber = this.$route.query.id;
    } else {
      this.pageNumber = "0";
    }
    this.init = true;
  }
}
</script>

<style lang="sass" scoped>
  .buttons
    display: flex
    justify-content: center
    margin-top: 10px
  .news > div
    text-align: center
    margin-top: 20px
    border: 1px solid #ccc
    padding: 30px
    border-radius: 3px
</style>