export default {
    actions: {
        async loadNews(ctx){
            let res = await fetch('https://vue-backend123.herokuapp.com/getNews');
            let json = await res.json();
            ctx.commit('setNews', json.data);
        },
        async deleteNews(ctx, id){
            let res = await fetch(`https://vue-backend123.herokuapp.com/deleteNews?id=${id}`, {
                method: 'DELETE'
            });
            let json = await res.json();
            ctx.commit('setNews', json.data);
        }
    },
    getters: {
        getNews: state => state.news,
    },
    state: {
        news: [],
    },
    mutations: {
        setNews(state, data){
            state.news = data;
        }
    },
}