<template>
  <div id="app">
      <div v-if="!init">
        loading...
      </div>
      <div v-else>
        <Header />
        <router-view />
      </div>
  </div>
</template>

<script>
import Header from "@/components/Header/Header";
import {mapActions} from 'vuex';
export default {
  name: 'App',
  components: {Header},
  data(){
    return {
      init: false,
    }
  },
  methods: mapActions(['loadNews']),
  async mounted() {
    if(!this.init){
      await this.loadNews();
      this.init = true;
    }
  }
}
</script>

<style lang="sass" scoped>
  body
    margin: 0
    padding: 0
</style>
